<template>
  <div class="start">
    <div class="game__num">
      {{num}}
    </div>
    <div class="game__text">
      <span v-show="showWord">
        <span id="game__word" v-if="num ">
          <span v-if="num!=spy">
            {{nowWord}}
          </span>
          <span v-else>Ти шпигун!</span>
        </span>
      </span>
      <div class="buttons">
        <a class="start__play" @click="startPlayer()">{{btnText}}</a>
        <!-- <a class="start__play" @click="nextPlayer()">Ок</a> -->
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'PlayGame',
    data() {
      return {
        name: 'Гра почалась',
        num: 1,
        nowWord: '---',
        spy: 1,
        showWord: false,
        btnText: 'Показати слово',
        words: [{
            id: 1,
            word: 'Вишневий сад',
            category: 'hard'
          },
          {
            id: 2,
            word: 'Епічне мяу',
            category: 'local'
          },
          {
            id: 3,
            word: 'Київ',
            category: 'place'
          },
          {
            id: 4,
            word: 'Концерт',
            category: 'place'
          },
          {
            id: 5,
            word: 'Цвинтар',
            category: 'place'
          },
        ],
      }
    },
    methods: {
      randomWord: function () {
        this.num = 1;
        console.log('called randomWord');
        let randomNum = Math.floor(Math.random() * this.words.length);
        let randomWordFull = this.words[randomNum].id + ', ' + this.words[randomNum].word + ', ' + this.words[
          randomNum].category;
        let randomWordShort = this.words[randomNum].word;

        this.nowWord = randomWordShort;
        this.spy = Math.floor(Math.random() * 3) + 1;
      },
      startPlayer: function () {
        if (this.showWord == false) {
          if (this.num == this.spy) {
            console.log(this.num + ' - ти ШПИГУН');
          } else {
            console.log(this.num + ' слово: ' + this.nowWord);
          }
          this.btnText = 'Добре';
          this.showWord = true;
        } else {
          this.btnText = 'Показати слово';
          this.showWord = false;
          this.num++;
        }
      },
      nextPlayer: function () {
        console.log('called nextPlayer');
        if (this.num >= 3) {
          console.log('ГРА ПОЧАЛАСЯ');
        } else {
          this.num++;

        }
      },
    },
    beforeMount() {
      this.randomWord();
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .start {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }

  .start-icon {
    font-size: 154px;
  }

  .game__text {
    font-size: 32px;
    width: 100%;
    text-align: center;
    margin-top: 15px;
    font-weight: 300;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .game__num {
    width: 30px;
    height: 30px;
    position: absolute;
    top: 70px;
    left: 20px;
    font-size: 16px;
    border: 1px solid white;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgba(255, 255, 255, .8);
  }

  #game_word {
    transition: 3s;
  }

  .hide {
    opacity: 0;
  }

  .show {
    opacity: 1;
  }

  .buttons {
    display: flex;
  }

</style>



